.multi-pack {
    display: flex;
    flex-wrap: wrap;

    >* {
        width: 270px;
        margin-right: 30px;
        margin-top: 40px;

        &:nth-child(4n+4) {
            margin-right: 0;
        }

        &:nth-child(-n+4) {
            margin-top: 0;
        }
    }

}