.auto-update-content {
    font-weight: 700;
    font-size: 14px;
    line-height: 18px;
    text-align: center;
    color: #055160;
    background: #cff4fc;
    height: 50px;
    border: 1px solid #9eeaf9;
    border-radius: 4px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
}

.live-zone {
    position: relative;
    width: 100%;

    .sticky-notification {
        >* {
            margin-top: 16px;

            &:first-child {
                margin-top: 0;
            }
        }

        margin-bottom: 35px;
        padding: 20px;
        background: rgba(255, 193, 7, 0.2);
        border: 1px dashed #FFDA6A;
        border-radius: 6px;

        .heading {
            font-style: normal;
            font-weight: bold;
            font-size: 18px;
            line-height: 28px;

        }

        .text {
            background: transparent;
            padding: 0;
        }

        .content {
            >* {
                margin-top: 16px !important;

                &:first-child {
                    margin-top: 0 !important;
                }
            }
        }
    }

    .wrap-item {
        >* {
            margin-top: 16px;

            &:first-child {
                margin-top: 0;
            }
        }
    }

    .item {

        >* {
            margin-top: 12px;

            &:first-child {
                margin-top: 0;
            }

            >* {
                margin-top: 12px;

                &:first-child {
                    margin-top: 0;
                }
            }
        }

        .time {
            font-family: "Quicksand";
            font-weight: 500;
            font-size: 16px;
            line-height: 30px;
            color: #bd1723;
        }

        .heading {
            font-weight: $font-weight-medium;
            font-size: 19px;
            line-height: 30px;
        }

        .content {
            >* {
                margin-top: 12px !important;
                font-size: 19px;
                line-height: 28px;


                &:first-child {
                    margin-top: 0 !important;
                }
            }
        }

        .meta {
            position: relative;

            &::after {
                position: absolute;
                content: "";
                width: 100%;
                height: 1px;
                background: #FFC107;
                top: 50%;
                transform: translateY(-50%);
                z-index: 0;
            }

            time {
                font-family: $font-family-branch;
                font-size: 16px;
                line-height: 21px;
                color: #212529;
                display: block;
                position: relative;
                z-index: 1;
                width: fit-content;
                background: #fff;
                padding: 0 16px;
                margin-left: 81px;
            }

            i {
                display: none;
            }
        }
    }

