// ----------------------------------
// INFO : must not be changed
// ----------------------------------
.zce-content-body {
    >* {
        margin-top: 16px !important;

        &:first-child {
            margin-top: 0;
        }
    }

    figure {
        text-align: center;
        width: 100%;

        .photo {
            display: block;

            img {
                max-width: 100%;
                width: auto;
                text-align: center;
                display: block;
                margin: 0 auto;
            }
        }

        figcaption {
            margin: 8px auto 0;
        }
    }

    .notebox {
        border: 1px dashed #333;
        color: #343a40;
        padding: 20px 23px;
    }

    .notebox.style-1 {
        background: #0b699e;
        color: #fff;
        border: 1px dashed #fff;
    }

    .notebox.style-2 {
        background: red;
        color: #fff;
        border: 1px dashed #fff;
    }

    .quote {
        padding: 30px 24px;
        margin: 10px 16px 20px;
        position: relative;
        border: 1px solid #08496e;
    }

    .related-topic {
        padding: 16px 20px;

        .box-heading {
            font-size: 14px;
            margin-bottom: 10px;
            color: #fff;
            text-decoration: none;

            * {
                color: #fff;
                text-decoration: none;
            }

            a {
                &:hover {
                    color: #fff;
                    text-decoration: none;
                }
            }
        }

        .story {
            margin-top: 10px;

            &:first-child {
                margin-top: 0;
            }
        }

        .story__heading {
            margin-bottom: 0;

            a {
                color: #971928;
                font-size: 16px;
                font-weight: 600;
                display: block;
                position: relative;
                padding-left: 16px;
                text-decoration: none;

                &::before {
                    content: "•";
                    display: inline-block;
                    font-size: 14px;
                    position: absolute;
                    left: 0;
                    top: 0;
                }
            }
        }
    }

    .full-width {
        max-width: calc(100vw - 17px);
        width: 100vw;
        left: 50%;
        position: relative;
        transform: translateX(-50%);
        right: auto;
    }

    // style class outset
    .outset-both {
        margin: 0 -24px;
        width: auto;
    }

    .outset-left {
        width: 50%;
        margin-left: -25%;
        margin-right: 16px;
        float: left;
    }

    .outset-right {
        width: 50%;
        margin-right: -25%;
        margin-left: 16px;
        float: right;
    }

    // style class align     
    .align-right {
        width: 50%;
        float: right;
        margin-left: 20px;
    }

    .align-left {
        width: 50%;
        float: left;
        margin-right: 20px;
    }

    .align-center {
        img {
            margin-left: auto;
            margin-right: auto;
            max-width: 80%;
        }
    }

    // related-content
    related-content.align-right {
        width: 50%;
        float: right;
        margin-left: 20px;
    }

    related-content.align-left {
        width: 50%;
        float: left;
        margin-right: 20px;
    }

    // article__relate
    .article__relate {
        background: #f0f0f0;
        overflow: auto;
    }

    .article__relate__thumb {
        width: 160px;
        height: 120px;
        float: left;
        margin-right: 16px;

        img {
            width: 100%;
            height: auto;
        }
    }

    .article__relate__heading {
        margin: 0;

        a {
            padding: 16px 16px 0 0;
            font-weight: 600;
            font-size: 18px;
            color: #333;
            line-height: 25px;
            text-decoration: none;
            display: block;
        }
    }


    // INFOR :style for table in box
    blockquote,
    .notebox {
        .outset-left {
            // left: calc(25%);
            margin-left: auto;
            margin-right: 16px;
        }

        .outset-right {
            margin-right: auto;
            margin-left: 16px;
            // margin: initial;
            // transform: translateX(0);
            // left: 0;
        }
    }
}

.zce-mobile {
    .zce-content-body {
        width: 392px;

        .full-width {
            max-width: 100%;
        }
    }

    [data-display="mobile"] {
        display: none;

        .zce-Visibility {
            display: none;
        }
    }

    [data-display="desktop"] {
        display: none;

        .zce-Visibility {
            display: none;
        }
    }
}

.zce-content-body.zce-mobile {
    .full-width {
        max-width: 100%;
    }
}

// support style các box blockquote, notebox, poll-box. Dùng riêng cho từng project
.zce-content-body {

    blockquote {
        padding: 16px;
        background: #ccc;
        font-weight: $font-weight-semibold;
        font-size: 22px;
        line-height: 31px;

        .author {
            margin: 30px auto 0;
            font-size: 14px;
            color: #343A40;
            font-weight: $font-weight-normal;
            display: block;
            padding-left: 42px;
            text-align: center;
            position: relative;
            width: fit-content;

            &::before {
                position: absolute;
                content: "";
                width: 30px;
                height: 1px;
                background: #212529;
                right: calc(50% + 42px);
                top: calc(50% - 1px);
                left: 0;
            }
        }

        &[align="left"] {
            padding-left: 0;
        }

        &.align-left {
            padding-left: 0;
        }

        &[align="right"] {
            padding-right: 0;
        }

        &.align-right {
            padding-right: 0;
        }
    }

    .notebox {
        background: #FFF3CD;
        border: 2px dashed #FFC107;
        border-radius: 8px;
        padding: 24px;
    }

    .poll-box {
        background: #FFFFFF;
        box-shadow: 1px 0px 50px rgba(0, 0, 0, 0.1);
        padding: 20px;
        display: block;
        text-align: left;
        margin-top: 40px;
        margin-bottom: 40px;

        .box-heading {
            >* {
                font-weight: 400;
                font-size: 14px;
                color: #343A40;
            }
        }

        .box-content {
            margin-top: 15px;

            .desc {
                font-weight: 500;
                font-size: 19px;
            }

            label {
                cursor: pointer;
                background: #F8F9FA;
                border-radius: 4px;
                padding: 12px;
                display: block;
                margin-top: 8px;
                font-weight: $font-weight-normal;
                font-size: 16px;
                line-height: 20px;

                input {
                    margin-right: 12px;
                }

                &:first-child {
                    margin-top: 0;
                }
            }
        }

        .wrap-button {
            margin-top: 15px;

            >* {
                min-width: 127px;
                height: 32px;
                padding: 6px 32px;
                border-radius: 6px;
                font-weight: $font-weight-medium;
                font-size: 14px;
                line-height: 20px;
            }

            .btn-send {
                background: #DC3545;
                color: #fff;
            }

            .btn-see {
                background: #fff;
                color: #000000;
            }
        }
    }
}