@import './zce-common-body.scss';

// ----------------------------------
// INFO : style layout 
// có 7 dạng hiển thị : left, right, center, outset-left, outset-right, full-width, max-width
// bỏ mixin w1300, w100 nếu ảnh default = nội dung bài viết
// ----------------------------------
@mixin classOutsetLeftRightDetail() {

    &.outset-left {
        margin-left: auto;
    }

    &.outset-right {
        margin-right: auto;
    }
}

@mixin maxWidthFullWiwthDetail() {

    &.full-width,
    &.max-width {
        width: 100%;
    }

}

// style table in side blockquote, notebox
@mixin table() {
    table {

        &.full-width,
        &.max-width {
            width: 100%;
        }
    }

    @include tableNotBorder();

}

@mixin space() {
    >* {
        margin-top: 16px;

        &:first-child {
            margin-top: 0;
        }
    }
}

@mixin tableNotBorder() {

    td,
    th {
        padding: 0 !important;
        border: none !important;
        display: block;
        align-items: center;
    }
}

@mixin classLeftRight() {

    &.align-left {
        width: 50%;
        float: left;
        margin-right: 16px;
        margin-bottom: 16px;
    }

    &.align-right {
        width: 50%;
        float: right;
        margin-left: 16px;
        margin-bottom: 16px;
    }

}

@mixin maxWidth() {
    &.max-width {
        position: relative;
        left: 50%;
        transform: translateX(-50%);
        width: calc(100vw - 17px);
        max-width: 1300px;
    }
}

@mixin alignLeftRight() {
    &[align="center"] {
        @include tableNotBorder();
        text-align: left;
        width: 100%;
    }

    &[align="left"] {
        width: 50%;
        float: left;
        margin-right: 16px;
        margin-bottom: 16px;
        @include tableNotBorder();
        text-align: left;
    }

    &[align="right"] {
        width: 50%;
        float: right;
        margin-left: 16px;
        margin-bottom: 16px;
        @include tableNotBorder();
        text-align: left;
    }
}

@mixin w1300() {
    &[align="center"] {
        position: relative;
        width: 1300px;
        left: 50%;
        transform: translateX(-50%);
    }
}

@mixin w100() {
    &[align="center"] {
        width: 100%;
    }
}

.zce-content-body {
    table {

        td,
        th {
            padding: 0;
        }
    }

    img,
    video {
        max-width: 100%;
        width: auto;
        display: flex;
        margin: 0 auto;
    }

    figcaption,
    .caption {
        font-size: 16px;
        line-height: 21px;
        color: #6C757D;
        text-align: center;
        margin-top: 8px;
        display: block;
        font-weight: $font-weight-normal;

        >* {
            margin-top: 0;
        }
    }

    table:not([class]) {
        width: 100%;

        td,
        th {
            padding: 10px;
            border: 1px solid #E9ECEF;
        }
    }

    figure,
    table,
    blockquote,
    .notebox,
    .poll-box {
        @include classLeftRight();
        @include alignLeftRight();
        @include maxWidth();
    }

    figure,
    table {
        // @include w1300();
    }

    blockquote,
    .notebox {
        @include space();
        @include table();

        // table,
        // figure {
        //     @include w100();
        // }

    }

    .live-zone,
    .interview-zone {

        table,
        figure {
            @include maxWidthFullWiwthDetail();
            @include classOutsetLeftRightDetail();
            // @include w100();
        }
    }
}

// for detail-page
.site-body {
    >.container:not(.longform) {
        .zce-content-body {

            figure,
            table,
            blockquote,
            .notebox,
            .poll-box {
                @include classOutsetLeftRightDetail();
            }

            figure,
            table {
                @include maxWidthFullWiwthDetail();
                @include w100();
            }

            blockquote {
                &.outset-left {
                    padding-left: 0;
                    padding-right: 60px;
                }

                &.outset-right {
                    padding-right: 0;
                    padding-left: 60px;
                }
            }
        }
    }
}

// ----------------------------------
// INFO : style các box trong bài viết
// ----------------------------------
.zce-content-body {

    // style article-relate 
    .article-relate {
        .story {
            display: flex;
            align-items: center;

            .row {
                display: block;
            }

            &__thumb {
                min-width: 330px;
                height: 220px;
                border-radius: 6px;
                margin-right: 30px;
            }

            &__heading a {
                font-weight: $font-weight-medium;
                font-size: 24px;
                line-height: 35px;
            }

            &__summary {
                margin-top: 10px;
                font-weight: $font-weight-normal;
                font-size: 15px;
                line-height: 23px;
                color: #6C757D;
            }
        }
    }

    // style related-topic
    .related-topic {
        margin: 40px !important;
        padding: 20px;
        box-shadow: 1px 0px 50px rgba(0, 0, 0, 0.1);
        width: 50%;
        float: right;

        .title {
            font-weight: $font-weight-semibold;
            font-size: 20px;
            line-height: 30px;
            text-transform: uppercase;
            color: #212529;
            padding-bottom: 8px;
            border-bottom: 1px solid #DC3545;
            margin-bottom: 15px;

            &:hover {
                color: $primary !important;
            }
        }

        .story {
            margin-top: 16px;
            padding-top: 16px;
            border-top: 1px solid #E9ECEF;

            &:first-child {
                margin-top: 0;
                padding-top: 0;
                border-top: none;

                .story__thumb {
                    float: left;
                    margin-right: 15px;
                }
            }

            &__thumb {
                width: 120px;
                height: 80px;
            }

            &__heading a {
                font-weight: $font-weight-semibold;
                font-size: 17px;
                line-height: 25px;
                color: #343A40;
                padding: 0;



                &::before {
                    display: none;
                }
            }
        }

    }

    // style related-news
    .related-news {

        .title {
            font-size: 20px;
            line-height: 30px;
            text-transform: uppercase;
            color: #212529;
            background: url(../img/bottom-decor_v2.png) bottom left no-repeat;
            padding-bottom: 10px;
            margin-bottom: 20px;
        }

        .story {
            margin-top: 12px;

            &:first-child {
                margin-top: 0;
            }

            &__heading a {
                font-weight: $font-weight-medium;
                font-size: 17px;
                line-height: 25px;
                color: #343A40;
                position: relative;
                padding-left: 20px;

                &::before {
                    position: absolute;
                    content: "";
                    width: 6px;
                    height: 6px;
                    border-radius: 50%;
                    background: #E9ECEF;
                    left: 0;
                    top: 9px;
                }
            }
        }

    }

    // style author
    .article__author {
        font-weight: $font-weight-medium;
        font-size: 17px;
        line-height: 25px;
        color: #343A40;
        text-align: right;
    }

}

// style tags
.article__tag {
    display: flex;
    align-items: center;

    .title {
        font-weight: $font-weight-medium;
        font-size: 15px;
        line-height: 24px;
        text-transform: uppercase;
        color: #000000;
    }

    .box-content {
        margin-left: 16px;
        display: flex;
        flex-wrap: wrap;

        >* {
            border: 1px solid #DEE2E6;
            border-radius: 8px;
            padding: 11px 8px;
            font-weight: 400;
            font-size: 15px;
            line-height: 8px;
            color: #212529;
            margin-left: 8px;

            &:hover {
                color: $primary;
            }

            &:first-child {
                margin-left: 0;
            }
        }
    }
}

.article__source {
    display: flex;
    flex-flow: wrap row;
    justify-content: flex-end;

    .wrap-source {
        display: flex;
        align-items: center;
        padding: 9px 12px;
        border-radius: 8px;
        position: relative;
        max-width: fit-content;
        margin-bottom: 16px;
        min-height: 50px;
        width: 100%;
        background: #f8f9fa;
        line-height: 20px;

        .avatar {
            overflow: hidden;
            width: 32px;
            min-width: 32px;
            height: 32px;
            border-radius: 50%;
            margin-right: 16px;
            background: #9e9e9e url(../img/copy.png) center center/14px no-repeat;

            img {
                width: 100%;
                color: transparent;
            }
        }

        .name {
            font-weight: $font-weight-medium;
            font-size: 14px;
            line-height: 20px;
            color: #495057;
        }

        .text {
            font-weight: $font-weight-normal;
            line-height: 20px;
            color: #6c757d;
            font-size: 14px;
        }
    }
}